"use client"
import { SalesChart } from "@/components/dashboard/sales-chart"
import { KeyMetrics } from "@/components/dashboard/key-metrics"
import { RecentAppointments } from "@/components/dashboard/recent-appointments"
import { IncomeExpenseChart } from "@/components/dashboard/income-expense-chart"
import { LoadingPage } from "@/components/ui/loader"
import { useAuth } from "@/contexts/auth-context"
import { useState, useEffect } from "react"

export default function DashboardPage() {
  const { getUserId, getClinicId } = useAuth()
  const [dashboardData, setDashboardData] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const userId = getUserId()
    const clinicId = getClinicId()

    console.log("[v0] User ID:", userId, "Clinic ID:", clinicId)

    if (userId && clinicId) {
      fetchDashboardData(userId, clinicId)
    } else {
      setLoading(false)
    }
  }, [getUserId, getClinicId])

  const fetchDashboardData = async (userId, clinicId) => {
    try {
      setLoading(true)
      console.log("[v0] Fetching dashboard data...")

      const response = await fetch(`/api/dashboard?userId=${userId}&clinicId=${clinicId}`)
      const result = await response.json()

      console.log("[v0] Dashboard data received:", result)

      if (result.success) {
        setDashboardData(result.data)
      } else {
        console.error("[v0] Error in response:", result.error)
      }
    } catch (error) {
      console.error("[v0] Error al cargar dashboard:", error)
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return <LoadingPage text="Cargando dashboard..." />
  }

  return (
    <div className="space-y-8">
      <div>
        <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
        <p className="text-muted-foreground">Resumen general de la cl칤nica</p>
      </div>

      <KeyMetrics data={dashboardData?.metrics} />

      <div className="grid gap-4 md:grid-cols-2">
        <SalesChart data={dashboardData?.salesData} />
        <IncomeExpenseChart data={dashboardData?.incomeExpenseData} />
      </div>

      <RecentAppointments data={dashboardData?.appointments} />
    </div>
  )
}
